'use client';
import { createContext, useContext, useEffect, useState } from 'react';
import api from '@/lib/axios';

type AuthContextType = {
  isAuthenticated: boolean;
  username: string | null;
  userImage: string | null;
  login: (user: { username: string; userImage?: string }) => void;
  logout: () => void;
};

const AuthContext = createContext<AuthContextType>({
  isAuthenticated: false,
  username: null,
  userImage: null,
  login: () => {},
  logout: () => {},
});

export const AuthProvider = ({ children }: { children: React.ReactNode }) => {
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [username, setUsername] = useState<string | null>(null);
  const [userImage, setUserImage] = useState<string | null>(null);

  const login = ({ username, userImage }: { username: string; userImage?: string }) => {
    setIsAuthenticated(true);
    setUsername(username);
    setUserImage(userImage || null);
  };

  const logout = async () => {
    await api.post('/api/users/logout');
    setIsAuthenticated(false);
    setUsername(null);
    setUserImage(null);
  };

  return (
    <AuthContext.Provider value={{ isAuthenticated, username, userImage, login, logout }}>
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => useContext(AuthContext);
